    <div class="home">
        <div class="swiper">
            <el-carousel height="800px" :interval="4000" >
              <el-carousel-item>
                  <div class="banner1 banner"></div>
              </el-carousel-item>
              <el-carousel-item>
                  <div class="banner2 banner"></div>
              </el-carousel-item>
            </el-carousel>
        </div>
        <div class="productions p-20">
            <div class="desc-box">
                <div class="title"></div>
                <div class="sub-title m-10">OUR PRODUCTS</div>
                <div class="desc m-30">FOF</div>
            </div>
            <el-row type="flex" justify="center" >
              <el-col :xl="6" :lg="7" :md="8" :sm="9" :xs="10" class="f-tac m-10">
                  <div class="prod-pic">
                      <img src="../assets/fof.png" alt="">
                  </div>
                  <div class="title m-10">FOF</div>
                  <div class="desc grey w-250 h-center">
                      ...
                  </div>
                  <el-button class="m-10 detail" size="small" icon="el-icon-arrow-right" ></el-button>
              </el-col>
              <el-col :xl="6" :lg="7" :md="8" :sm="9" :xs="10" class="f-tac m-10">
                  <div class="prod-pic">
                      <img src="../assets/trade.png" alt="">
                  </div>
                  <div class="title m-10"></div>
                  <div class="desc grey w-250 h-center">
                      ...
                  </div>
                  <el-button class="m-10 detail" size="small" icon="el-icon-arrow-right" ></el-button>
              </el-col>
            </el-row>
        </div>
        <div class="solutions p-20">
            <div class="desc-box">
                <div class="title"></div>
                <div class="sub-title m-10">SOLUTION</div>
                <div class="desc m-30"></div>
            </div>
            <div class="f-tac f-cb" >
                <div class="sol-pic sol-pic-1">
                    <img src="../assets/sol1.png" alt="">
                    <div class="intro" data-text="" ></div>
                </div>
                <div class="sol-pic sol-pic-2">
                    <img src="../assets/sol2.png" alt="">
                    <div class="intro" data-text="" ></div>
                </div>
                <div class="sol-pic sol-pic-3">
                    <img src="../assets/sol3.png" alt="">
                    <div class="intro" data-text="" ></div>
                </div>
            </div>
        </div>
        <div class="cooperation p-20">
            <div class="desc-box">
                <div class="title"></div>
                <div class="sub-title m-10">CLIENT & PARTNER</div>
            </div>

        </div>
        <div class="about">
            <div class="f-tac f-cb" >
                <div class="par-pic">
                    <img src="../assets/fr-logo.png" alt="">
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                </div>
                <div class="par-pic">
                    <img style="margin-left:-20px;" src="../assets/xdl-logo.png" alt="">
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                </div>
                <div class="par-pic">
                    <p class="par par-1"></p>
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                    <div class="border"></div>
                </div>
                <div class="par-pic">
