<script>
// @ is an alias to /src

export default {
    data() {
        return {
            
        }
    },
    methods: {
        
    }
};
</script>

<style>

.prod-pic {
    /* width: 200px; */
    /* height: 200px; */
    /* background-color: #8d8d8d; */
    margin: 0 auto;
    perspective: 200px;
}
.prod-pic:hover {
    /* perspective: 300px; */
}
.prod-pic img {
    height: 200px;
    transition: all .3s;
    transition-timing-function: cubic-bezier(0.55, 0.03, 0.515, 0.955)
}
.prod-pic:hover img {
    transform: rotate3d(0, 1, 0, 10deg);
}

.sol-pic {
    position: relative;
    display: inline-block;
    width: 360px;
    height: 290px;
    background-color: #723f3f48;
    margin-left: 10px;
    overflow: hidden;
    transition: all .3s;
}
.sol-pic:hover::before, .sol-pic:hover::after {
    transform: translate3d(0, 100%, 0)
}
.sol-pic:hover::after {
    transition-delay: 0.175s;
}
.sol-pic::before, .sol-pic::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    bottom: 100%;
    left: 0;
    z-index: 1;
    transition: transform .3s;
    transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1)
}
.sol-pic::before {
    background: #ffffff17;
}
.sol-pic::after {
    background: #d3c8eb4b;
}
.sol-pic img {
    transition: all .3s;
}

.sol-pic:hover img {
    transform: scale(1.2)
}

.sol-pic .intro {
    color: #ffffff8c;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 1s;
    z-index: 2;
}
.sol-pic:hover .intro {
    color: #fff;
    /* transform: scale(1.2); */
    /* transform-origin: 250% 250%; */
    /* transform-origin: 250% 250%; */
}
/* .sol-pic .intro::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #ffffff00;
    transform-origin: -25% 50%;
    transform: rotate3d(0, 0, 1, 45deg);
    transition: all 0.3s ease-in;
}
.sol-pic:hover .intro::after {
    transform: rotate3d(0, 0, 1, 0deg);
    transition-timing-function: ease-out;
    color: #fff;
} */
.banner1 {
    background: url(../assets/banner1.png) no-repeat center top;
}
.banner2 {
    background: url(../assets/banner2.png) no-repeat center top;
}
.banner {
    height: 800px;
    /* background-size: 100% 100%; */
}

.detail {
    transition: all .3s;
}
.detail:hover {
    background: #3f51b5;
}

.detail span {
    display: inline-block;
    transition: all .3s;
    transform: translate3d(0, 0, 0);
    margin-left: 0 !important;
}
.detail:hover span {
    color: #fff;
    transform: translate3d(10px, 0, 0);
}

.detail i {
    position: absolute;
    transition: all .3s;
    transform: translate3d(-40px, 2.5em, 0);
    color: #fff;
}
.detail:hover i {
    transform: translate3d(-5px, 1px, 0);
}

.par-pic {
    position: relative;
    display: inline-block;
    width: 250px;
    height: 200px;
    line-height: 200px;
    /* background-color: #723f3f48; */
    margin-left: 10px;
    overflow: hidden;
    transition: all .3s;
    /* border: 1px solid #e2e2e2; */
    background-color: #c1ccce1a;
}
.par-pic img {
    vertical-align: middle;
}
.par-pic .par {
    
    font-size: 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 1s;
    z-index: 2;
}
.par-1 {
    color: #d8aa2c;
}
.par-2 {
    color: #2c85d8;
}
.par-pic::before {
    content: " ";
    display: block;
    width: 400px;
    height: 80px;
    background: rgba(0, 146, 214, 0.123);
    position: absolute;
    top: 50px;
    left: -350px;
    transform: rotate(-45deg);
    transition: all .3s ease-in-out;
}
.par-pic:hover::before {
    margin-left: 500px;
}

.par-pic .border {
    position: absolute;
    background: none;
    transition: all .3s ease-in-out;
}
.par-pic .border:nth-of-type(1) {
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 1px solid #aaa;
}
.par-pic .border:nth-of-type(2) {
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-right: 1px solid #aaa;
}
.par-pic .border:nth-of-type(3) {
    bottom: 0;
    right: 0;
    width: 0;
    height: 0;
    border-bottom: 1px solid #aaa;
}
.par-pic .border:nth-of-type(4) {
    bottom: 0;
    left: 0;
    width: 0;
    height: 0;
    border-left: 1px solid #aaaaaa;
}
.par-pic:hover .border:nth-of-type(even) {
    height: 200px;
}
.par-pic:hover .border:nth-of-type(odd) {
    width: 250px;
}
</style>




